import React, { Component } from 'react'
import { Button, Input, Modal, ModalBody, ModalFooter } from 'reactstrap'
import Spinner from '../../components/@vuexy/spinner/Fallback-spinner'
import { toast } from 'react-toastify'
import DataTable from 'react-data-table-component'
import classnames from 'classnames'
import { history } from '../../history'
import { Edit, Info, Trash, ChevronDown } from 'react-feather'
import { connect } from 'react-redux'
import {
  getData,
  deleteData,
  updateData,
  addData,
  filterData
} from '../../redux/actions/data-list/'
import 'react-toastify/dist/ReactToastify.css'
import '../../assets/scss/plugins/extensions/toastr.scss'
import Sidebar from './DataListSidebar'
import axios from 'axios'
import Chip from '../../components/@vuexy/chips/ChipComponent'
import '../../assets/scss/pages/data-list.scss'
import '../../assets/scss/components/app-loader.scss'

axios.defaults.baseURL = 'https://lastarzi2.herokuapp.com'
//axios.defaults.baseURL = "http://localhost:5000";

const selectedStyle = {
  rows: {
    selectedHighlighStyle: {
      backgroundColor: 'rgba(115,103,240,.05)',
      color: '#7367F0 !important',
      boxShadow: '0 0 1px 0 #7367F0 !important',
      '&:hover': {
        transform: 'translateY(0px) !important'
      }
    }
  }
}

const DeleteComponent = (props) => {
  return (
    <div className="data-list-action">
      <Trash
        style={{
          cursor: props?.row?.usage === 'Unused' ? 'pointer' : 'no-drop'
        }}
        size={window.screen.width < 600 ? 13 : 20}
        onClick={() => {
          props?.row?.usage === 'Unused' && props.setRow()
        }}
      />
    </div>
  )
}

const EditComponent = (props) => {
  return (
    <div className="data-list-action">
      {window.screen.width > 600 ? (
        <Edit
          className="cursor-pointer mr-1"
          size="13"
          onClick={() => {
            return props.currentData(props.row)
          }}
        />
      ) : (
        <Info
          className="cursor-pointer mr-1"
          size="20"
          onClick={() => {
            return props.currentData(props.row)
          }}
        />
      )}
    </div>
  )
}

const CustomHeader = (props) => {
  return (
    <div className="data-list-header d-flex justify-content-between flex-wrap">
      <div className="actions-left d-flex">
        <Button.Ripple
          color="white"
          className="sort-dropdown mx-50"
          style={{
            height: '75%',
            backgroundColor: 'var(--warning)',
            fontWeight: 'bold'
          }}
          outline
          onClick={() => props.handleSidebar(true, true)}
        >
          {/* <Plus size={15} /> */}
          <span className="align-middle">Add</span>
        </Button.Ripple>
      </div>
      <div className="actions-right d-flex flex-wrap">
        <div className="filter-section">
          <Input
            type="text"
            style={{ height: '75%', borderRadius: '5rem', fontSize: '1rem' }}
            onChange={(e) => props.handleFilter(e)}
            placeholder="Find Nominee"
            className="placeholder"
          />
        </div>
      </div>
      <div className="">
        <div className="data-list-rows-dropdown mr-1 d-none">
          <Button.Ripple
            color=""
            className="sort-dropdown"
            style={{ height: '75%' }}
          >
            <span className="align-middle mx-50">Count : {props.total}</span>
          </Button.Ripple>
        </div>
      </div>
    </div>
  )
}

class DataListConfig extends Component {
  static getDerivedStateFromProps(props, state) {
    if (
      props.dataList.data.length !== state.data.length ||
      state.currentPage !== props.parsedFilter?.page
    ) {
      return {
        data: props.dataList.data,
        allData: props.dataList.filteredData,
        //totalPages: props.dataList.totalPages,
        totalPages: props.dataList.data.length / 5,
        currentPage: parseInt(props.parsedFilter?.page) - 1,
        rowsPerPage: parseInt(props.parsedFilter?.perPage),
        totalRecords: props.dataList.totalRecords,
        sortIndex: props.dataList.sortIndex,
        dataLoading: false
      }
    }

    // Return null if the state hasn't changed
    return null
  }

  state = {
    data: [],
    totalPages: 0,
    currentPage: 0,
    columns: [
      {
        name: 'Edit',
        sortable: false,
        width: '80px',
        cell: (row) => (
          <EditComponent
            row={row}
            getData={this.props.getData}
            parsedFilter={this.props.parsedFilter}
            currentData={this.handleCurrentData}
          />
        )
      },
      {
        name: 'Name',
        selector: 'name',
        sortable: true,
        minWidth: '200px',
        cell: (row) => (
          <p title={row.name} className="text-truncate text-bold-500 mb-0">
            {row.name}
          </p>
        )
      },
      {
        name: 'Relation',
        selector: 'relation',
        sortable: true,
        width: '170px'
      },
      {
        name: 'Email',
        minWidth: '200px',
        selector: 'email',
        sortable: true
      },
      {
        name: 'Contact',
        selector: 'primaryContact',
        minWidth: '150px',
        sortable: true
      },
      {
        name: 'Status',
        width: '120px',
        selector: 'unused',
        sortable: true,
        cell: (row) => (
          <Chip
            className="m-0"
            color={row.usage === 'Unused' || !row.usage ? 'primary' : 'success'}
            text={row.usage === 'Unused' || !row.usage ? 'Unused' : 'Used'}
          />
        )
      },
      {
        name: 'Delete',
        sortable: false,
        width: '110px',
        cell: (row) => (
          <DeleteComponent
            row={row}
            setRow={() => {
              this.setState({ open: true })
              this.setState({ deleteThisRow: row })
            }}
            getData={this.props.getData}
            parsedFilter={this.props.parsedFilter}
            deleteRow={this.handleDelete}
          />
        )
      }
    ],
    mobilecolumns: [
      {
        name: 'Info',
        sortable: true,
        width: '50px',
        cell: (row) => (
          <EditComponent
            row={row}
            getData={this.props.getData}
            parsedFilter={this.props.parsedFilter}
            currentData={this.handleCurrentData}
            deleteRow={this.handleDelete}
          />
        )
      },
      {
        name: 'Name',
        selector: 'name',
        sortable: true,
        minWidth: '50px',
        cell: (row) => (
          <p
            style={{ fontSize: '10px' }}
            title={row.name}
            className="text-truncate text-bold-500 mb-0"
          >
            {row.name}
          </p>
        )
      },
      {
        name: 'Relation',
        selector: 'relation',
        maxWidth: '50px',
        sortable: true,
        cell: (row) => (
          <p
            style={{ fontSize: '10px' }}
            title={row.relation}
            className="text-truncate text-bold-500 mb-0"
          >
            {row.relation}
          </p>
        )
      },
      {
        name: 'Trash',
        sortable: true,
        width: '50px',
        cell: (row) => (
          <DeleteComponent
            row={row}
            getData={this.props.getData}
            parsedFilter={this.props.parsedFilter}
            currentData={this.handleCurrentData}
            deleteRow={this.handleDelete}
          />
        )
      }
    ],
    deleteThisRow: null,
    open: false,
    allData: [],
    value: '',
    rowsPerPage: 4,
    sidebar: false,
    currentData: null,
    selected: [],
    totalRecords: 0,
    sortIndex: [],
    addNew: '',
    isLoading: false,
    dataLoading: false
  }

  thumbView = this.props.thumbView

  componentDidMount() {
    !this?.props?.dataList?.data?.length &&
      !this.props.listLoading &&
      this.props.getData()
    this.props.dataTest()
  }

  handleFilter = (e) => {
    this.setState({ value: e.target.value })
    this.props.filterData(e.target.value)
  }

  handleRowsPerPage = (value) => {
    let { parsedFilter, getData } = this.props
    let page = parsedFilter?.page !== undefined ? parsedFilter?.page : 1
    history.push(`/nominee/list?page=${page}&perPage=${value}`)
    this.setState({ rowsPerPage: value })
    getData({ page: parsedFilter?.page, perPage: value })
  }

  handleSidebar = (boolean, addNew = false) => {
    this.setState({ sidebar: boolean })
    if (addNew === true) this.setState({ currentData: null, addNew: true })
  }

  handleDelete = (row) => {
    this.props.deleteData(row)
    if (this.state.data.length - 1 === 0) {
      let urlPrefix = this.props.thumbView
        ? '/data-list/thumb-view/'
        : '/#/nominee/list/'
      history.push(
        `${urlPrefix}list-view?page=${parseInt(
          this.props.parsedFilter?.page - 1
        )}&perPage=${this.props.parsedFilter.perPage}`
      )
    }
  }
  addData = async (obj) => {
    this.setState({ isLoading: true })
    try {
      await this.props.addData(obj)
      this.setState({ isLoading: false })
      this.handleSidebar(false, true)
    } catch {
      this.setState({ isLoading: false })
      toast.error('Add Nominee Failed! Please contact admin')
    }
  }
  updateData = async (obj) => {
    if (
      obj.name === '' ||
      obj.relation === '' ||
      obj.email === '' ||
      obj.contact1 === '' ||
      obj.address === ''
    ) {
      toast.error('Please Fill All Mandatory Details')
    } else {
      this.setState({ isLoading: true })
      try {
        await this.props.updateData(obj)
        this.setState({ isLoading: false })
        this.handleSidebar(false, true)
      } catch {
        this.setState({ isLoading: false })
        toast.error('Unable to update! Please try again')
      }
    }
  }
  handleCurrentData = (obj) => {
    this.setState({ currentData: obj })
    this.handleSidebar(true)
  }

  handlePagination = (page) => {
    let { parsedFilter, getData } = this.props
    let perPage = parsedFilter.perPage !== undefined ? parsedFilter.perPage : 4
    let urlPrefix = this.props.thumbView
      ? '/#/nominee/list/'
      : '/#/nominee/list/'
    history.push(
      `${urlPrefix}list-view?page=${page.selected + 1}&perPage=${perPage}`
    )
    getData({ page: page.selected + 1, perPage: perPage })
    this.setState({ currentPage: page.selected })
  }

  render() {
    let {
      columns,
      data,
      allData,
      value,
      rowsPerPage,
      currentData,
      sidebar,
      totalRecords,
      sortIndex
    } = this.state
    console.log('3333', this.props.listLoading)
    if (this.props.listLoading) {
      return (
        <div className="fallback-spinner vh-100">
          <Spinner color="danger" size="sm" />
        </div>
      )
    }
    return (
      <div
        className={`data-list ${
          this.props.thumbView ? 'thumb-view' : 'list-view'
        }`}
      >
        <Modal
          isOpen={this.state.open}
          className="modal-dialog-centered"
          backdrop="static"
        >
          <ModalBody>Are you sure you want to delete this record?</ModalBody>
          <ModalFooter>
            <Button
              color="primary"
              onClick={() => this.setState({ open: false })}
              className="button-label"
            >
              Cancel
            </Button>
            <Button
              color="danger"
              onClick={() => {
                this.handleDelete(this.state.deleteThisRow)
                this.setState({ open: false })
              }}
              className="button-label"
            >
              Delete
            </Button>
          </ModalFooter>
        </Modal>

        <DataTable
          width="200"
          columns={
            window.screen.width < 600 ? this.state.mobilecolumns : columns
          }
          data={value.length ? allData : data}
          noHeader
          subHeader
          selectableRows={window.screen.width < 600 ? false : false}
          responsive
          pointerOnHover
          selectableRowsHighlight
          onSelectedRowsChange={(data) =>
            this.setState({ selected: data.selectedRows })
          }
          customStyles={selectedStyle}
          subHeaderComponent={
            <CustomHeader
              className="data-table"
              handleSidebar={this.handleSidebar}
              handleFilter={this.handleFilter}
              handleRowsPerPage={this.handleRowsPerPage}
              rowsPerPage={rowsPerPage}
              total={totalRecords}
              index={sortIndex}
            />
          }
          sortIcon={<ChevronDown />}
        />
        <Sidebar
          show={sidebar}
          data={currentData}
          updateData={this.updateData}
          addData={this.addData}
          isLoading={this.state.isLoading}
          handleSidebar={this.handleSidebar}
          thumbView={this.props.thumbView}
          getData={this.props.getData}
          dataParams={this.props.parsedFilter}
          addNew={this.state.addNew}
        />
        <div
          className={classnames('data-list-overlay', {
            show: sidebar
          })}
          onClick={() => this.handleSidebar(false, true)}
        />
      </div>
    )
  }
}

const mapStateToProps = (state) => {
  console.log(
    'state.dataList.totalRecordsLoading',
    state.dataList.totalRecordsLoading
  )
  return {
    dataList: state.dataList,
    totalRecordsLoading: state.dataList.totalRecordsLoading
  }
}
const mapDispatchToProps = (dispatch) => {
  return {
    deleteData: (state) => dispatch(deleteData(state)),
    updateData: (state) => dispatch(updateData(state)),
    addData: (state) => dispatch(addData(state)),
    getData: (state) => dispatch(getData(state)),
    filterData: (data) => dispatch(filterData({ data })),
    dataTest: () => dispatch({ type: 'GET_ALL_DATA_LOADING', data: true })
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(DataListConfig)
